	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Frozen In","date":"25 Dec 2002","slug":"/2002-12-25-frozen-in/","tags":"Y2002"},"html":"<p>The ice has only gotten worse. It rained and thawed this afternoon, only to freeze again this evening. Large BMWs with rear-wheel drive are <em>not</em> able to negotiate ice-coated cobblestones, as Mama found out when taking Oma Ruth home. The BMW is now parked at the bottom of our street, waiting for better traction.</p>\n<p>All guests were able to make it to the Christmas feast, and more importantly, all were able to make it home safely.</p>"},"next":{"fields":{"title":"Blitz Ice","date":"24 Dec 2002","slug":"/2002-12-24-blitz-ice/"}},"previous":{"fields":{"title":"Holiday Greetings","date":"26 Dec 2002","slug":"/2002-12-26-holiday-greetings/"}}},{"node":{"fields":{"title":"Blitz Ice","date":"24 Dec 2002","slug":"/2002-12-24-blitz-ice/","tags":"Y2002"},"html":"<p>Severe freezing rain is mucking up the works around here for Christmas. We've had a 1 cm layer of ice covering everything all day long here, and tomorrow promises to be more of the same. We'll see if the whole family can make it in for the big goose feast. But Christopher's happy. He's already scored a ton of train gear, and there's more coming tomorrow, and again on his birthday.</p>"},"next":{"fields":{"title":"Shopping List","date":"23 Dec 2002","slug":"/2002-12-23-shopping-list/"}},"previous":{"fields":{"title":"Frozen In","date":"25 Dec 2002","slug":"/2002-12-25-frozen-in/"}}},{"node":{"fields":{"title":"Shopping List","date":"23 Dec 2002","slug":"/2002-12-23-shopping-list/","tags":"Y2002"},"html":"<ul>\n<li>Wine glasses. We have 6, but we need 8. Might as well go to Ikea and get a set.</li>\n<li>I'm at Ikea anyway, so pick up a couple of Lack shelves for my brother-in-law.</li>\n<li>Goose breast, on special at Minimal, so Mama and I can have a private Christmas dinner on the 26th even if the goose from the 25th is history.</li>\n<li>Salami, as long as I'm at Minimal anyway.</li>\n<li>Potatoes for baking, for the 24th. We have plenty of regular pototoes, but no big ones.</li>\n<li>A sled for Christopher? Probably not, the weather is going to thaw and turn slushy.</li>\n</ul>"},"next":{"fields":{"title":"Creating an MT Import File from HTML","date":"23 Dec 2002","slug":"/2002-12-23-creating-an-mt-import-file-from-html/"}},"previous":{"fields":{"title":"Blitz Ice","date":"24 Dec 2002","slug":"/2002-12-24-blitz-ice/"}}}],"pathPrefix":"","first":false,"last":false,"index":802,"pageCount":1101}}

